
import { useState } from "react";
import Countdown from "react-countdown";
import Particles from "react-particles";
import { Typewriter } from "react-simple-typewriter";
import { loadFireworksPreset } from "tsparticles-preset-fireworks";

function App() {
  const [newYearMessage, setNewYearMessage] = useState(["Bye 2023", "Welcome 2024"])

  const particleInitialization = async (engine) => {
    await loadFireworksPreset(engine)
  }
  function timeLeft() {
    const newYearDate = new Date("January 3, 2024 00:00:00").getTime()
    const nowDate = new Date().getTime()
    const remainingTime = newYearDate - nowDate
    return remainingTime
  }
  return (
    <>
      <Particles
        init={particleInitialization}
        options={{ preset: "fireworks" }}
      />
      <div className="flex flex-col  justify-center items-center min-h-screen gap-x-2 sm:gap-y-4">
        <span className=" text-center text-white text-4xl font-bold px-4 z-50">
          <Typewriter
            words={newYearMessage}
            loop={false}
            cursorStyle={"_"}
            cursor
            typeSpeed={20}
            delaySpeed={10}
          />
        </span>
        <div className="z-50 text-white">
          <Countdown date={Date.now() + timeLeft()} />
        </div>
      </div>




    </>
  );
}

export default App;
